* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  width: 50%;
  background: linear-gradient(45deg, #1870ed 0, #f18f88 100%);
  border-radius: 5px;
  justify-content: center;
  margin: 30px auto;
  padding: 20px;
  display: flex;
  box-shadow: 1px 1px 5px rgba(255, 255, 255, .15), 0 15px 90px 30px rgba(0, 0, 0, .25);
}

.btn {
  cursor: pointer;
  color: #fff;
  border-radius: 6px;
  justify-content: center;
  padding: 6px 24px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-flex;
}

.btn--play {
  text-shadow: 0 1px #aade7c;
  background: #77d42a linear-gradient(#77d42a 5%, #5cb811 100%);
  border: 1px solid #268a16;
  margin-right: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-100%, -50%);
  box-shadow: inset 0 1px #caefab;
}

.btn--play:hover {
  background: #5cb811 linear-gradient(#5cb811 5%, #77d42a 100%);
}

.btn--stop {
  text-shadow: 0 1px #b23e35;
  background: #fe1a00 linear-gradient(#fe1a00 5%, #ce0100 100%);
  border: 1px solid #d83526;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(100%, -50%);
  box-shadow: inset 0 1px #f29c93;
}

.btn--stop:hover {
  background: #ce0100 linear-gradient(#ce0100 5%, #fe1a00 100%);
}

.field {
  text-align: center;
  min-width: 90px;
  flex-direction: column;
  margin-left: 40px;
  margin-right: 40px;
  display: flex;
  position: relative;
}

.field:not(:last-child):before, .field:not(:last-child):after {
  content: "";
  height: 5px;
  width: 5px;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 50%;
  display: block;
  position: absolute;
  right: -42px;
}

.field:not(:last-child):before {
  top: 35%;
}

.field:not(:last-child):after {
  top: 50%;
}

.value {
  color: #fff;
  text-transform: uppercase;
  font-size: 4.2rem;
}

.label {
  color: rgba(255, 255, 255, .35);
  text-transform: uppercase;
  margin-top: 10px;
  font-size: .7rem;
}

